/* Media Query Variable */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital@0;1&family=Oswald:wght@200..700&family=Plus+Jakarta+Sans:ital@0;1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital@0;1&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Oswald:wght@200..700&family=Plus+Jakarta+Sans:ital@0;1&display=swap");
/*CSS Variable*/
:root {
  /*font Variable*/
  --ff-oswald: "Oswald", sans-serif;
  --ff-openSans: "Open Sans", sans-serif;
  /*Color Variable*/
  --primary-orange: #f99e23;
  --color-black: #000;
  --color-light-black: #303030;
}

a {
  color: unset;
  text-decoration: none;
}
a:focus {
  outline: 0 solid;
}
a:hover {
  text-decoration: none;
  color: unset;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
  font-family: var(--ff-oswald);
  color: var(--color-black);
  font-weight: 600;
  line-height: 1.3;
}

h1,
.h1 {
  font-size: 65px;
  line-height: 1.05;
  letter-spacing: 0px;
  text-transform: none;
  margin-bottom: 0;
}

h4,
.h4 {
  font-size: 45px;
  line-height: 1.1;
  letter-spacing: 0px;
  text-transform: none;
  margin-bottom: 0;
}

h6,
.h6 {
  font-family: var(--ff-openSans);
  font-size: 24px;
  color: var(--color-light-black);
  line-height: 1.15;
  letter-spacing: 0px;
  text-transform: none;
  margin-bottom: 0;
}

@media only screen and (max-width: 1199.98px) {
  h1,
  .h1 {
    font-size: 54px;
  }
  h4,
  .h4 {
    font-size: 35px;
  }
}
@media only screen and (max-width: 991.98px) {
  h1,
  .h1 {
    font-size: 40px;
  }
  h4,
  .h4 {
    font-size: 24px;
  }
  h6,
