2. Menu CSS
=================================================*/
.menu-area {
  position: relative;
  width: 100%;
  padding: 18px 0px;
  z-index: 9;
  background: var(--color-black);
}
.menu-area.sticky {
  position: fixed;
  animation: slide_down_site 0.35s ease-out;
  background-color: var(--color-black);
  top: 0;
  left: 0;
}

.transparent-header {
  position: absolute;
  background: transparent;
  top: 0;
  left: 0;
}

.admin-bar .menu-area {
  top: 32px;
}

@media screen and (max-width: 781.99px) {
  .admin-bar .menu-area,
  .admin-bar .transparent-header {
    top: 46px;
  }
}
@media screen and (max-width: 600px) {
  .admin-bar .menu-area,
  .admin-bar .transparent-header {
    top: 46px;
  }
  .admin-bar .menu-area.sticky {
    top: 0;
  }
}
@keyframes slide_down_site {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.mainmenu-wrap {
  gap: 33px;
}

.mainmenu {
  text-align: right;
}
.mainmenu ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: end;
  gap: 33px;
}
.mainmenu ul ul {
  display: block;
}
.mainmenu ul ul li ul {
  left: -215px;
  top: 64px;
}
.mainmenu ul ul li:hover > ul {
  visibility: visible;
  opacity: 1;
  top: 15px;
}
.mainmenu li {
  display: inline-block;
  position: relative;
  flex-shrink: 0;
}
.mainmenu li a {
  padding: 9px 0;
  transition: 0.3s;
  display: inline-block;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  color: #fff;
}
.mainmenu li.active a {
  color: #7da8a9;
}
.mainmenu li ul {
  position: absolute;
  background: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
  min-width: 292px;
  height: auto;
  transform-origin: 0 0 0;
  right: 0px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s;
  z-index: 9;
  padding: 10px 13px;
  transform: scaleY(0);
}
.mainmenu li ul li {
  text-align: left;
  display: block;
  padding: 3px;
  position: relative;
  margin-left: 12px;
  line-height: 32px;
}
.mainmenu li ul li:not(:last-child) {
  border-bottom: 1px dashed #000;
  margin-bottom: 7px;
  padding-bottom: 7px;
}
.mainmenu li ul li::before {
  position: absolute;
  left: -20px;
  font-size: 19px;
  top: 50%;
  transform: translateY(-50%);
  content: "\f285";
  font-family: bootstrap-icons !important;
  opacity: 0;
  transition: all 0.3s;
}
.mainmenu li ul li:hover::before {
  opacity: 1;
}
.mainmenu li ul li > a {
  transition: 0.3s;
}
.mainmenu li ul a {
  color: var(--color-black);
  width: 85%;
  text-align: left;
  margin-left: 0px;
  padding: 0px 0px;
}
.mainmenu li:hover > a {
  color: #7ca8aa;
  transition: all 0.3s;
}
.mainmenu li:hover ul {
  visibility: visible;
  opacity: 1;
  transform: scaleY(1);
}
.mainmenu li:hover ul ul {
  visibility: hidden;
  opacity: 0;
}
.mainmenu li.has-child-menu > a {
  position: relative;
}
.mainmenu li.has-child-menu > a::before {
  position: absolute;
  left: 106%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  font-family: bootstrap-icons !important;
  content: "\f282";
  transition: all 0.5s;
  color: #c99a2c;
}
.mainmenu li.has-child-menu:hover a::before {
  transform: translateY(-50%) rotate(180deg);
}
.mainmenu li.has-child-menu ul li.has-child-menu > a::before {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 10px;
  font-family: bootstrap-icons !important;
  -webkit-text-stroke: 1px;
  content: "\f285";
  transition: all 0.5s;
  color: #000;
}

